@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@600&display=swap);
* {
  font-family: -apple-system, BlinkMacSystemFont, 'Spartan', sans-serif;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background-color: #fff7e3 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
  text-align: center;
  background-color: #fbc84f;
  align-items: center;
  bottom: 0;
  width: 100%;
  padding: 1% 0;
}

.footer-link {
  color: black !important;
  text-decoration: none !important;
}

.footer-link:hover {
  color: white !important;
}

@media only screen and (max-width: 386px) {
  .footer {
      padding: 0;
  }

  .footer-link {
      font-size: 0.8em;
  }
}

.menu {
    background-color: #039490 !important;
    padding: 2% 0 1% 0;
    /* background-color: #FBC84F !important; */
    top: 0 !important; 
    position: sticky !important;
    width: 100%;
}

.menu-nav {
    display: flex !important;
    align-items: center !important;
    width: 100%;
}

.menu-nav-title-sup, .menu-nav-title-inf {
    color: white !important;
    font-size: 1.7em;
    margin-right: 3%;
}

.menu-nav-title-inf { display: none; }

@media only screen and (max-width: 386px) {
    .menu-nav-title-sup {
        display: none !important;
    }
    .menu-nav-title-inf {
        display: block !important;
        font-size: 1em;
    }
}

.menu-nav-bar-btn {
    color: black !important;
    background-color: #FBC84F !important;
    padding:  10% 30% !important;
    margin: 4% 4%;
    border-radius: 15em ;
}

.menu-nav-bar-btn:hover {
    -webkit-transform: scale(0.98) !important;
            transform: scale(0.98) !important;
    -webkit-transform-style: preserve-3d !important;
            transform-style: preserve-3d !important;
    color: white !important;
    background-color: #E6B236 !important;
    
}

@media only screen and (max-width: 386px){
    .menu-nav-bar-btn {
        padding: 5% 2%!important;
        text-align: center;
        font-size: 1em;
        
    }
}

.App {
    text-align: center;
}

.content {
    align-content: center;
    background-color: white !important;
    margin: 0 5% !important;
    height: 100%;
    width: auto;
    text-overflow: ellipsis;
}

.content p {
    text-align: center;
    font-size: 3rem;
    color: #039490;
    padding: 2% 0 0 0;
    font-weight: bold;
}

.content-sub {
    font-size: 1.8rem !important;
    margin: 0 20%;
}

@media only screen and (max-width: 386px){
    .content p{
        padding: 5% 0 0 0;
        font-size: 1.2rem;
    }

    .content-sub {
        margin: 0 15%;
        font-size: 0.9rem !important;
    }
}

